<template>
  <v-app>
    <v-app-bar color="primary" class="align-start h-2">
      <div v-on:click="goTo()">
        <img :src="`${publicPath}logo_colors.png`" class="object-contain w-1/2" />
      </div>
    </v-app-bar>
    <router-view :users="users" @addUser="addUser" />
    <BottomNav />

  </v-app>
</template>

<script>
import BottomNav from ".//components/BottomNav";
import usersJSON from "./data/users.json";
import User from "./classes/User";

export default {
  name: "App",
  emits: ["addUser"],

  components: {
    BottomNav,
  },

  data: function () {
    return {
      publicPath: process.env.BASE_URL
    };
  },

  computed: {
    users() {
      let usersArray = [];
      for (let user of usersJSON.users) {
        usersArray.push(
          new User(
            user.id,
            user.firstName,
            user.lastName,
            user.userImage,
            user.long,
            user.lat,
            user.gamesOwned
          )
        );
      }
      return usersArray;
    },

  },

  methods: {

    addUser: function (user) {
      this.users.push(user);
    },
    goTo() {
      this.$router.push(`/`);
    },
  },
};
</script>